import './App.css';
import BackgroundUACJ from './componentes/Background';
import CategoryUACJ from './componentes/CategoryContainer/CategoryContainer';
import EventUACJ from './componentes/EventUACJ';
import FooterUACJ from './componentes/Footer';
import NavBarUACJ from './componentes/NavBar';

import { Spacer } from "@nextui-org/react";
import { Accordion, AccordionItem } from "@nextui-org/react";

function App() {
  return (
    <div className="App">
      <BackgroundUACJ>
        <NavBarUACJ></NavBarUACJ>
        <h1 className="text-xl font-bold text-white" >Prximos eventos!</h1>
        <Spacer y={6} />
        <div className="flex flex-wrap justify-center items-center">
          <CategoryUACJ
            image={`${process.env.PUBLIC_URL}/Photos/CoverPage/Deportivos.jpeg`}
            title='Eventos deportivos'
          >
            <Accordion>
              <AccordionItem key="1" aria-label="Ping Pong" title="Ping Pong">
                <EventUACJ
                  link={`${process.env.PUBLIC_URL}/Files/Convocatoria torneo de ping pong.docx`}
                  info={'El Instituto de Ingeniera y Tecnologa de la UACJ invita a la comunidad universitaria a participar en un torneo de Ping Pong en conmemoracin de 50 aos de ingeniera en la institucin. El evento se llevar a cabo los das viernes 12, 19 y 26 de abril y 3 de mayo en la Plaza de la Fraternidad del instituto.'}
                  qr={`${process.env.PUBLIC_URL}/Photos/Logos/QR's/PingPong.png`}
                />
              </AccordionItem>
              <AccordionItem key="2" aria-label="Ftbol" title="Ftbol">
              <EventUACJ
                  link={`${process.env.PUBLIC_URL}/Files/Convocatoria torneo mixto de futbol IIT.docx`}
                  info={'Torneo recreativo de Ftbol calle mixto en conmemoracin de sus 50 aos de ingeniera. El evento se llevar a cabo los das viernes 12, 19 y 26 de abril y 3 de mayo en la Plaza de la Fraternidad del instituto. Se invita a formar equipos mixtos de hasta 8 integrantes, con dos jugadoras y dos jugadores en el cuadro en juego.'}
                  qr={`${process.env.PUBLIC_URL}/Photos/Logos/QR's/FutBol.png`}
                />
              </AccordionItem>
            </Accordion>
          </CategoryUACJ>
          <CategoryUACJ
            image={`${process.env.PUBLIC_URL}/Photos/CoverPage/Culturales.jpg`}
            title='Eventos culturales'
          ></CategoryUACJ>
          <CategoryUACJ
            image={`${process.env.PUBLIC_URL}/Photos/CoverPage/Arduino.jpg`}
            title='Eventos ingenieria'
          >
            <Accordion>
              <AccordionItem key="1" aria-label="Mouseke-olimpiadas" title="Mouseke-olimpiadas">
                <EventUACJ
                  link={`${process.env.PUBLIC_URL}/Files/Mousek.docx`}
                  info={'Competencia de Mouseke-olimpiadas como parte de las celebraciones del 30 Aniversario del Instituto, el 06 de mayo de 2024, de 10:00 AM a 02:00 PM en el vestbulo Y-4. Se considerarn tres competidores por equipo, inscritos en el semestre actual de licenciatura, con un representante (profesor) por equipo.'}
                  qr={`${process.env.PUBLIC_URL}/Photos/Logos/QR's/MouseK.png`}
                />
              </AccordionItem>
              <AccordionItem key="2" aria-label="Maratn de Programacin y Algoritmia" title="Maratn de Programacin y Algoritmia">
                <EventUACJ
                    link={`${process.env.PUBLIC_URL}/Files/Reglamento Maraton de programacion y algoritmia.docx`}
                    info={'El maratn ser dividido en dos categoras, Algoritmia se llevar a cabo el 9 de abril de 2024, de 10:00 de la maana a 6:00 de la tarde, mientras que el de Programacin se celebrar el 9 y 10 de abril del presente ao, en el Instituto de Ingeniera y Tecnologa (IIT) y en Ciudad Universitaria, los eventos sern simultneos en los institutos y contarn con un receso.'}
                  />
              </AccordionItem>
              <AccordionItem key='3' aria-label='Concurso Catapulta' title='Concurso Catapulta'>
                <EventUACJ
                  link={`${process.env.PUBLIC_URL}/Files/convocatoria concurso catapultas  ene_jun 2024_1.pdf`}
                  info={'El Instituto de Ingeniera y Tecnologa, junto con la Rama estudiantil ASQ-UACJ, invita a estudiantes universitarios a participar en el concurso La Catapulta, que busca fomentar el aprendizaje en estadstica industrial y calidad. Los equipos de 4 a 6 integrantes analizarn y optimizarn el rendimiento de una catapulta en dos fases. El evento se llevar a cabo el 6 de mayo de 2024, otorgando premios en efectivo a los mejores proyectos.'}
                  qr={`${process.env.PUBLIC_URL}/Photos/Logos/QR's/Catapulta.png`}
                />
              </AccordionItem>
            </Accordion>
          </CategoryUACJ>
        </div>
        <Spacer y={6} />
        <FooterUACJ></FooterUACJ>
      </BackgroundUACJ>
    </div>
  );
}

export default App;
